import { Link } from "react-router-dom"

function Sidebar() {
    return (
        <div className="ml-10">
            <Item name="Home" icon="home" url="/"/>
            <Item name="Card" icon="home" url="card"/>
            <Item name="Search" icon="home" url="search"/>
        </div>
    )
}

function Item(props) {
    return (
        <div>
            <div className="cursor-pointer font-medium p-3 flex items-center hover:text-red-500 d-flex">
                <div className="flex-grow flex items-center">
                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" className="h5 w-5">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 4h13M3 8h9m-9 4h9m5-4v12m0 0l-4-4m4 4l4-4" />
                    </svg>
                    <Link className="ml-2" to={props.url}>{props.name}</Link>
                </div>
            </div>
        </div>
    )
}

export default Sidebar