import { Route, Switch } from "react-router-dom";
import Search from "../components/search";
import Card from "../components/card";
import Home from "../components/home";

export default function Main() {
    return (
        <Switch>
            <Route path="/search">
                <Search />
            </Route>
            <Route path="/search">
                <Card />
            </Route>
            <Route path="/">
                <Home />
            </Route>
        </Switch>
    )
}